import formLocations from "./formLocations";
import { POST, DELETE } from '../../api/index'
import { HalfCircleSpinner } from "epic-spinners";
import { success, deleteData, error } from "../../service/MessageBox"

export default {
  name: "client",
  components: { formLocations, HalfCircleSpinner },
  data() {
    return {
      isLoading: false,
      isShow: false,
      pagination: {
        page: 1,
        rowsPerPage: 5
      },
      totalData: 0,
      disableView: false,
      disableEdit: false,
      textTitle: "",
      dataLocation: [],
      form: {
        Id: null,
        LocationCode: null,
        Address: null,
        City: null,
        Province: null,
        Description: null,
        LocationSize: null,
      },
      headers: [{
        text: "Aksi",
        value: "aksi",
        sortable: false,
        align: "left",
        class: ""
      }, {
        text: "Location Code",
        value: "LocationCode",
        align: "center"
      }, {
        text: "Address",
        value: "Address",
        align: "center"
      }, {
        text: "City",
        value: "City",
        align: "center"
      }, {
        text: "Province",
        value: "Province",
        align: "center"
      }, {
        text: "Description",
        value: "Description",
        align: "center"
      }, {
        text: "Location Size",
        value: "LocationSize",
        align: "center"
      }]
    };
  },
  created() {
    this.loadData()
  },
  beforeMount: function () { },
  computed: {},
  methods: {
    toggleInputDialog: function () {
      this.isShow = !this.isShow;
      this.textTitle = "Tambah Location"
    },
    onClose: function () {
      this.isShow = false;
      this.disableEdit = false;
      this.disableView = false;
      this.resetForm()
    },
    loadData: function () {
      try {
        this.isLoading = true
        const { page, rowsPerPage } = this.pagination;
        POST(`location/location_cr/${page}/${rowsPerPage}`).then((res) => {
          this.dataLocation = res.Data.map(x => {
            return ({
              Id: x.Id,
              LocationCode: x.LocationCode,
              Address: x.Address,
              City: x.City,
              Province: x.Province,
              Description: x.Description,
              LocationSize: x.LocationSize,
            })
          })
          this.totalData = res.TotalData;
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
        error("Error")
      }
    },
    onEdit: function (id) {
      try {
        this.isLoading = true
        POST('location/location_cr', { Id: id }).then((res) => {
          this.form = res[0]
          this.disableView = false;
          this.disableEdit = true;
          this.textTitle = "Ubah Location"
          this.isShow = true;
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
      }
    },
    onDelete: function (id) {
      deleteData(id, this.delData)
    },
    delData: function (id) {
      try {
        this.isLoading = true
        DELETE(`location/${id}`).then(() => {
          success("Data Berhasil Dihapus")
          this.loadData()
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
        error("Error")
      }
    },
    onView: function (id) {
      try {
        this.isLoading = true;
        POST('location/location_cr', { Id: id }).then((res) => {
          this.form = res[0]
          this.disableView = true;
          this.isShow = true;
          this.textTitle = "Lihat Location"
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
      }
    },
    resetForm: function () {
      this.form = {
        Id: null,
        LocationCode: null,
        Address: null,
        City: null,
        Province: null,
        Description: null,
        LocationSize: null,
      }
    }
  },
  watch: {
    pagination: {
      handler() {
        try {
          this.isLoading = true;
          const { page, rowsPerPage } = this.pagination;
          POST(`location/location_cr/${page}/${rowsPerPage}`).then(res => {
            this.dataLocation = res.Data.map(x => {
              return {
                Id: x.Id,
                LocationCode: x.LocationCode,
                Address: x.Address,
                City: x.City,
                Province: x.Province,
                Description: x.Description,
                LocationSize: x.LocationSize,
              };
            });
            this.isLoading = false;
            this.totalData = res.TotalData;
          });
        } catch (err) {
          this.isLoading = false;
          error("Error");
        }
      },
      deep: true
    }
  }
};
