import { POST, PUT } from '../../../api/index'
import { HalfCircleSpinner } from "epic-spinners";
import { success, error } from "../../../service/MessageBox"

export default {
  name: "input-client",
  components: { HalfCircleSpinner },
  props: ["form", "onClose", "loadData", "textTitle", "disableView", "disableEdit"],
  data() {
    return {
      isLoading: true
    };
  },
  beforeMount: function () { },
  mounted() { },
  methods: {
    onSave: function () {
      try {
        this.isLoading = true
        let dataReq = {
          Id: this.form.Id,
          LocationCode: this.form.LocationCode,
          Address: this.form.Address,
          City: this.form.City,
          Province: this.form.Province,
          Description: this.form.Description,
          LocationSize: this.form.LocationSize,
        }
        if (dataReq.Id === null) {
          POST("location", dataReq).then(() => {
            this.loadData()
            this.isLoading = false
            success("Data Berhasil Disimpan")
            this.onClose();
          })
        } else {
          PUT("location", dataReq).then(() => {
            this.loadData()
            this.isLoading = false
            success("Data Berhasil Di Update")
            this.onClose();
          })
        }
      } catch (err) {
        this.isLoading = false
        error("Error")
      }
    },
  }
};
